import React from 'react'
import { connect } from 'react-redux'
import { loadWeather } from '../../modules/weather/actions'

import './HomePage.scss';
import MainWeather from '../../cmps/MainWeather/MainWeather'
import WeatherList from '../../cmps/WeatherList/WeatherList'

class  HomePage extends React.Component {
    state = {
        weatherToday: [],
        nextDays: []
    }

    async componentDidMount() {
        await this.props.loadWeather()
        console.log(this.props)
        const { weatherToday } = await this.props.weather
        const { nextDays } = await this.props.weather
        await this.setState({ weatherToday, nextDays })
    }

    render() {
        const { weatherToday } = this.state;
        const { nextDays } = this.state;

        return (
            <>
                {weatherToday && nextDays && <>
                    <MainWeather weather={weatherToday} />
                    <WeatherList nextDays={nextDays} weatherToday={weatherToday} />
                </>}
            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        weather: state.weather,
    }
}

const mapDispatchToProps = {
    loadWeather,
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(HomePage)